import {Component, OnInit} from '@angular/core';
import {ActivatedRoute, NavigationExtras, Router} from '@angular/router';
import {AlertController, ToastController} from '@ionic/angular';
import {Food} from 'src/app/client1/models/food.model';
import {SubFood} from 'src/app/client1/models/subFood.model';
import {CartService} from 'src/app/client1/services/cart.service';
import {FoodAdminService} from 'src/app/client1/services/food-admin.service';
import {SubFoodAdminService} from '../../../services/sub-food-admin.service';

@Component({
  selector: 'app-list-sub-food',
  templateUrl: './list-sub-food.page.html',
  styleUrls: ['./list-sub-food.page.scss'],
})
export class ListSubFoodPage implements OnInit {
  id: number;
  foods: Food[] = [];
  food: Food;
  foood: Food;
  fod: Food[];
  message = '';
  subFood?: SubFood[];
  constructor(
    private activatedRoute: ActivatedRoute,
    private foodService: FoodAdminService,
    private subFoodService: SubFoodAdminService,
    private cartService: CartService,
    private toastCtrl: ToastController,
    private router: Router,
    private route: ActivatedRoute,
    private alertCtrl: AlertController,
  ) {
    this.id = +this.activatedRoute.snapshot.paramMap.get('id');
  }
  ngOnInit() {

    this.getFoodById(this.id);
    this.getSubFoodsByParent(this.id);
  }
  async getSubFoodsByParent(id: number) {
    this.subFoodService.getAllSubFoodByFood(id)
      .subscribe(
        data => {
          this.subFood = data;
          console.log('list-sub-food', data);
        },
        error => {
          console.log(error);
        });
  }
  getFoodById(id: number): void {
    this.foodService.getFoodsById(this.id)
      .subscribe(
        data => {
          this.food = data;
          console.log('parentFood is: ', data);
        },
        error => {
          console.log(error);
        });
  }

  async presentToast() {
    const toast = await this.toastCtrl.create({
        message: 'Food added to the cart',
        mode: 'ios',
        duration: 1000,
        position: 'bottom',
        color: 'success'
      }
    );
    toast.present();
    // @ts-ignore
    this.router.navigate(['/admin/listing']);
  }

  async confirmationEdit() {
    const toast = await this.toastCtrl.create({
        message: 'Updated successfully!',
        mode: 'ios',
        duration: 1000,
        position: 'bottom',
        color: 'success'
      }
    );
    toast.present();
    // @ts-ignore
    this.router.navigate(['admin/listing']);
  }

  removeFood(idFood: number): void {
    this.subFoodService.delete(idFood)
      .subscribe(
        response => {
          console.log(response);
        },
        error => {
          console.log(error);
        });
  }

  editSubFood(id: number){
    console.log('edit'+id);
  }

  async deleteSubFood(id: number) {
    const alert = await this.alertCtrl.create({
      header: 'Remove',
      message: 'Are you sure you want to remove?',
      buttons: [
        {
          text: 'Yes',
          handler: () => {
            this.removeFood(id);
            window.location.reload();
          }
        },
        {
          text: 'No',
        },
      ],
    });

    alert.present();
  }

  editItem(id: number) {
    //this.router.navigate(['/client1/admin/edit-sub-food', id]);

    const navigationExtras: NavigationExtras = {
      state: {
        sendId: this.id
      }
    };
    this.router.navigate(['/admin/edit-sub-food', id], navigationExtras);
  }

  addSubItem(){
    this.router.navigate(['/admin/add-sub-food', this.id]);

    //window.alert('your are in id'+ id +'& title: '+title);

  }
}
